body {
    font-family: 'Arial', sans-serif;
    background: linear-gradient(135deg, #2C3E50, #4CA1AF);
    margin: 0;
    padding: 20px;
    text-align: center;
    color: #fff; /* Texto branco para melhor contraste */
}

h1 {
    color: #ecf0f1; /* Cor mais clara para o título */
    margin-bottom: 10px;
    font-size: 2.5em; /* Tamanho maior */
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5); /* Sombra suave */
    animation: fadeIn 1s ease; /* Animação de entrada */
}

h2 {
    color: #bdc3c7; /* Cor mais clara para subtítulo */
    margin-bottom: 20px;
    font-size: 1.8em; /* Tamanho maior */
    animation: slideIn 1s ease; /* Animação de entrada */
}

.links {
    margin-top: 30px; /* Aumentei o espaçamento superior */
}

a {
    display: inline-block;
    margin: 10px;
    padding: 12px 24px; /* Aumentei o padding para botões mais largos */
    text-decoration: none;
    color: white;
    background-color: #FF6F61; 
    border-radius: 8px; /* Bordas mais arredondadas */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* Sombra para os botões */
    transition: background-color 0.3s, transform 0.2s, box-shadow 0.3s; /* Transições suaves */
}

a:hover {
    background-color: #FF4C3B; 
    transform: translateY(-2px); /* Efeito de elevação ao passar o mouse */
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3); /* Sombra mais pronunciada ao passar o mouse */
}

/* Animações */
@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes slideIn {
    from {
        transform: translateY(-20px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}

/* Adicionando um efeito de pulsação nos links */
a:active {
    animation: pulse 0.5s; /* Animação de pulsação ao clicar */
}

@keyframes pulse {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.05);
    }
    100% {
        transform: scale(1);
    }
}
